import React, { useState } from 'react';
import * as S from './style';
import ProfileImage from "../../assets/profile.svg";
import RandomName from './RandomName';
import { DodamFilledButton } from '@b1nd/dds-web';
import CommentItem from './CommentItem';
import { useCommentQuery, useCommentMutation, useDeleteCommentMutation } from '../../queries/Comment/comment.query';
import { B1ndToast } from '@b1nd/b1nd-toastify';
import { useQueryClient } from 'react-query';

interface CommentProps {
  postId: number;
  canDelete?: boolean;
}

const Comment = ({ postId,canDelete = false  }: CommentProps) => {
  const [authorName, setAuthorName] = useState("");
  const [content, setContent] = useState("");
  const deleteMutation = useDeleteCommentMutation();
  const { data, refetch } = useCommentQuery(postId);
  const mutation = useCommentMutation();
  const queryClient = useQueryClient();

  const handleChangeRandomName = (newName: string) => {
    setAuthorName(newName);
  };

  const handleSubmit = () => {
    if (!content.trim()) return B1ndToast.showInfo("  .");

    mutation.mutate(
      { postId, authorName, content },
      {
        onSuccess: () => {
          setContent("");
          queryClient.invalidateQueries(['comments', postId]);
          refetch();
        },
        onError: () => {
          B1ndToast.showError("  .");
        },
      }
    );
  };

  const handleDelete = (commentId: number) => {

    deleteMutation.mutate(commentId, {
      onSuccess: () => {
        B1ndToast.showSuccess(" .");
        queryClient.invalidateQueries(['comments', postId]);
        refetch();
      },
      onError: () => {
        B1ndToast.showError("  .");
      },
    });
  };

  return (
    <S.CommentBox>
      <S.CommentHeader>
      <span> {data?.totalCount ?? 0}</span>
        <span> : mdev_team@dgsw.hs.kr</span>
      </S.CommentHeader>
      <S.CommentInputBox>
        <S.CommentName>
          <img src={ProfileImage} alt=" " />
          <RandomName onChange={handleChangeRandomName}/>
        </S.CommentName>
        <S.CommentInput 
          placeholder='     .  , ,        .'
          value={content}
          onChange={(e) => setContent(e.target.value)}
        />
          <S.CommentButton>
            <DodamFilledButton
              width={112}
              size='Medium'
              text=' '
              onClick={() => handleSubmit()}
              backgroundColorType='Primary'
              textTheme='staticWhite'
            />
          </S.CommentButton>
      </S.CommentInputBox>
      
      {Array.isArray(data?.comments) && data?.comments.map((comment) => (
      <CommentItem
        key={comment.commentId}
        authorName={comment.authorName}
        content={comment.content}
        onDelete={canDelete ? () => handleDelete(comment.commentId) : undefined}

      />
    ))}
      
    </S.CommentBox>
  );
}


export default Comment;